<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="orderQueryInfo.query"
            @clear="inputCleared"
            clearable
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="searchOrders"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" stripe style="width: 100%" border="">
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="order_number" label="" width="500">
        </el-table-column>
        <el-table-column prop="order_price" label=""> </el-table-column>
        <el-table-column label="">
          <template v-slot:default="slotProps">
            <el-tag type="danger" v-if="slotProps.row.pay_status"
              ></el-tag
            >
            <el-tag type="success" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""> </el-table-column>
        <el-table-column label="" width="200">
          <template v-slot:default="slotProps">
            {{ slotProps.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="150">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="editDialog"
          ></el-button>
          <el-button
            type="success"
            icon="el-icon-location"
            size="mini"
            @click="showDialog"
          ></el-button>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="orderQueryInfo.pagenum"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="orderQueryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="orderTotal"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClose">
      <el-form
        :model="editAddressForm"
        :rules="editAddressFormRule"
        ref="editAddressFormRef"
        label-width="100px"
      >
        <el-form-item label="//" prop="address1">
          <el-cascader
            v-model="editAddressForm.address1"
            :options="addressData"
            style="width: 100%"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="editAddressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editDialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
  title=""
  :visible.sync="showDialogVisible"
  width="50%">
   <el-timeline>
    <el-timeline-item
      v-for="(activity, index) in locationList"
      :key="index"
      :timestamp="activity.time">
      {{activity.context}}
    </el-timeline-item>
  </el-timeline>
  <span slot="footer" class="dialog-footer">
    <el-button @click="showDialogVisible = false"> </el-button>
    <el-button type="primary" @click="showDialogVisible = false"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
import addressData from './citydata.js'
export default {
  data () {
    return {
      orderQueryInfo: {
        pagenum: 1,
        pagesize: 10,
        query: ''
      },
      orderTotal: 0,
      orderList: [],
      //   
      editDialogVisible: false,
      editAddressForm: {
        address1: [],
        address2: ''
      },
      editAddressFormRule: {
        address1: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //   
      addressData: addressData,
      //   
      showDialogVisible: false,
      locationList: []
    }
  },
  created () {
    this.getOrderList()
  },
  methods: {
    //   
    inputCleared () {
      this.getOrderList()
    },
    // 
    async getOrderList () {
      const { data: res } = await this.$http.get('orders', {
        params: this.orderQueryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      //   console.log(res)
      this.orderTotal = res.data.total
      this.orderList = res.data.goods
    },
    // 
    searchOrders () {
      this.getOrderList()
    },
    // 
    handleSizeChange (size) {
      this.orderQueryInfo.pagesize = size
      this.getOrderList()
    },
    // 
    handleCurrentChange (currentSize) {
      this.orderQueryInfo.pagenum = currentSize
      this.getOrderList()
    },
    editDialog () {
      this.editDialogVisible = true
    },
    // 
    editDialogClose () {
      this.$refs.editAddressFormRef.resetFields()
    },
    // 
    async getLocationInfo () {
      const { data: res } = await this.$http.get('/kuaidi/804909574412544580')
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      //   console.log(res.data)
      this.locationList = res.data
    },
    showDialog () {
      this.getLocationInfo()
      this.showDialogVisible = true
    }
  }
}
</script>

<style lang="less" scoped>
</style>
