<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-button type="primary" @click="addRoles"></el-button>
      <!--  -->
      <el-table
        :data="roleList"
        border
        style="width: 100%; margin-top: 20px"
        stripe
      >
        <!--  -->
        <el-table-column type="expand">
          <template v-slot:default="roleProps">
            <el-row
              v-for="(item1, i1) in roleProps.row.children"
              :key="item1.id"
              :class="['rowBottom', 'rowCenter', i1 === 0 ? 'rowTop' : '']"
            >
              <!--  -->
              <el-col :span="6">
                <el-tag
                  @close="deleteRights(roleProps.row, item1.id)"
                  closable
                  >{{ item1.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="18">
                <el-row
                  :class="['rowCenter', i2 === 0 ? '' : 'rowTop']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="8">
                    <el-tag
                      type="success"
                      @close="deleteRights(roleProps.row, item2.id)"
                      closable
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="16">
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      @close="deleteRights(roleProps.row, item3.id)"
                      closable
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="roleName" label=""> </el-table-column>
        <el-table-column prop="roleDesc" label=""> </el-table-column>
        <el-table-column label="">
          <template v-slot:default="roleProps">
            <el-button type="primary" icon="el-icon-edit" @click="editRolesDialog(roleProps.row)" size="mini"
            ></el-button
          >
          <el-button type="danger" icon="el-icon-delete" size="mini"
          @click="deleteRole(roleProps.row)"
            ></el-button
          >
          <el-button
            type="warning"
            icon="el-icon-setting"
            @click="setRightsDialog(roleProps.row)"
            size="mini"
            ></el-button
          >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRightsDialogVisible"
      width="50%"
    >
      <el-tree :data="roleRightsList" :props="roleRightsProps" node-key="id" :default-checked-keys="defaultKeys" ref="treeRef" default-expand-all show-checkbox></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightsDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"
          > </el-button
        >
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
  title=""
  @close="resetAddFields"
  :visible.sync="addRolesDialogVisible">
  <el-form ref="addRolesFormRef" :model="addRolesInfo" :rules="addRolesRules" label-width="80px">
  <el-form-item label="" prop="roleName">
    <el-input v-model="addRolesInfo.roleName"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model="addRolesInfo.roleDesc"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="addRolesDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addRolesHandle"> </el-button>
  </span>
</el-dialog>
<!--  -->
<el-dialog
  title=""
  @close="resetEditFields"
  :visible.sync="editRolesDialogVisible">
  <el-form ref="editRolesFormRef" :model="addRolesInfo" :rules="addRolesRules" label-width="80px">
  <el-form-item label="" prop="roleName">
    <el-input v-model="addRolesInfo.roleName"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model="addRolesInfo.roleDesc"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editRolesDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editRolesHandle"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      roleList: [],
      setRightsDialogVisible: false,
      // 
      roleRightsList: [],
      roleRightsProps: {
        children: 'children',
        label: 'authName'
      },
      // 
      defaultKeys: [],
      // id
      roleId: '',
      // 
      addRolesDialogVisible: false,
      // 
      addRolesInfo: {
        roleName: '',
        roleDesc: ''
      },
      // 
      addRolesRules: {
        roleName: [{ required: true, message: '', trigger: 'blur' },
          { min: 2, max: 7, message: ' 2  7 ', trigger: 'blur' }]
      },
      // 
      editRolesDialogVisible: false
    }
  },
  created () {
    this.getRoleList()
  },
  methods: {
    //   
    async getRoleList () {
      const { data: res } = await this.$http.get('roles')
      //   console.log(res)
      if (res.meta.status === 200) {
        this.roleList = res.data
        // this.$message.success(res.meta.msg)
      } else {
        this.$message.error(res.meta.msg)
      }
    },
    // 
    deleteRights (role, rightId) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        // console.log(role.id, rightId)
        const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
        if (res.meta.status === 200) {
          role.children = res.data
          this.$message({
            type: 'success',
            message: '!'
          })
        } else {
          this.$message.error(res.meta.msg)
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    async setRightsDialog (role) {
      this.defaultKeys = []
      this.roleId = role.id
      // console.log(this.roleId)
      const { data: res } = await this.$http.get('rights/tree')
      // console.log(res)
      if (res.meta.status === 200) {
        this.roleRightsList = res.data
        // console.log(role)
        this.getLeafKeys(role, this.defaultKeys)
      } else {
        this.$message.error(res.meta.msg)
      }
      this.setRightsDialogVisible = true
    },
    // id
    getLeafKeys (node, arr) {
      if (node.children) {
        node.children.forEach(item => {
          this.getLeafKeys(item, arr)
        })
      } else {
        return arr.push(node.id)
      }
      // console.log(arr)
    },
    // 
    async allotRights () {
      const keyArr = [...this.$refs.treeRef.getCheckedKeys(), ...this.$refs.treeRef.getHalfCheckedNodes()]
      // console.log(keyArr)
      const keyStr = keyArr.join()
      // console.log(this.roleId)
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, {
        rids: keyStr
      })
      if (res.meta.status === 200) {
        // console.log()
        this.getRoleList()
        this.setRightsDialogVisible = false
        this.$message.success(res.meta.msg)
      } else {
        this.$message.error(res.meta.msg)
      }
    },
    // 
    resetAddFields () {
      this.$refs.addRolesFormRef.resetFields()
      this.addRolesInfo = {}
    },
    // 
    addRoles () {
      this.addRolesDialogVisible = true
    },
    // 
    addRolesHandle () {
      this.$refs.addRolesFormRef.validate(async addRolesFlag => {
        // console.log(addRolesFlag)
        if (addRolesFlag) {
          const { data: res } = await this.$http.post('roles', this.addRolesInfo)
          if (res.meta.status === 201) {
            this.getRoleList()
            this.$message.success(res.meta.msg)
          } else {
            return this.$message.error(res.meta.msg)
          }
        } else {
          this.$message.error('')
        }
      })
      this.addRolesDialogVisible = false
    },
    // 
    async getRolesInfo (id) {
      const { data: res } = await this.$http.get(`roles/${id}`)
      // console.log(res)
      if (res.meta.status === 200) {
        this.addRolesInfo = res.data
      } else {
        return this.$message.error(res.meta.msg)
      }
    },
    // 
    editRolesDialog (role) {
      this.getRolesInfo(role.id)
      this.roleId = role.id
      // console.log(this.addRolesInfo)
      this.editRolesDialogVisible = true
    },
    // 
    editRolesHandle () {
      this.$refs.editRolesFormRef.validate(async editRolesFlag => {
        // console.log(editRolesFlag)
        if (editRolesFlag) {
          const { data: res } = await this.$http.put(`roles/${this.roleId}`, this.addRolesInfo)
          if (res.meta.status === 200) {
            this.getRoleList()
            this.$message.success('')
          } else {
            this.$message.error(res.meta.msg)
          }
        } else {
          return this.$message.error('')
        }
      })
      this.editRolesDialogVisible = false
      // console.log(this)
    },
    resetEditFields () {
      this.$refs.editRolesFormRef.resetFields()
      this.addRolesInfo = {}
    },
    // 
    deleteRole (role) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`roles/${role.id}`)
        if (res.meta.status === 200) {
          this.getRoleList()
          this.$message({
            type: 'success',
            message: '!'
          })
        } else {
          this.$message.error(res.meta.msg)
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 10px;
}

.rowBottom {
  border-bottom: 1px solid #eee;
}

.rowTop {
  border-top: 1px solid #eee;
}

.rowCenter {
  display: flex;
  align-items: center;
}
</style>
