/* eslint-disable vue/valid-template-root */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <el-input
        placeholder=""
        v-model="paramsUser.query"
        @change="searchUser"
        @clear="searchUser"
        clearable
      >
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="searchUser"
        ></el-button>
      </el-input>
      <el-button type="primary" @click="dialogVisible = true"
        ></el-button
      >
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="50%"
        @close="resetUserForm"
      >
        <el-form
          :model="userForm"
          :rules="userFormRule"
          ref="userFormRef"
          label-width="100px"
        >
          <el-form-item label="" prop="username">
            <el-input v-model="userForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="userForm.password" type="password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="userForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="userForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-table :data="usersData" border style="width: 100%" stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="username" label="" width="180">
        </el-table-column>
        <el-table-column prop="email" label=""> </el-table-column>
        <el-table-column prop="mobile" label="" width="180">
        </el-table-column>
        <el-table-column prop="role_name" label="" width="180">
        </el-table-column>
        <el-table-column label="" width="180">
          <template v-slot:default="slotProps">
            <el-switch
              v-model="slotProps.row.mg_state"
              active-color="#13ce66"
              inactive-color="#ff4949"
              @change="toggleSwitch(slotProps.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column prop="handle" label="" width="180">
          <template v-slot:default="slotProps">
            <!--  -->
            <el-button
              @click="trimUser(slotProps.row.id)"
              type="primary"
              icon="el-icon-edit"
              circle
            ></el-button>
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                @click="setRoles(slotProps.row)"
                circle
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-button type="danger" icon="el-icon-delete" @click="deleteUser(slotProps.row.id)" circle></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="paramsUser.pagenum"
        :page-sizes="[1, 2, 3, 5]"
        :page-size="paramsUser.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalUsers"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="trimUserVisible" @close="resetTrimUser" width="50%">
      <!--  -->
      <el-form
        :model="trimUserForm"
        :rules="userFormRule"
        ref="trimUserRef"
        label-width="100px"
      >
        <el-form-item label="">
          <el-input v-model="trimUserForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="trimUserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="trimUserForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="trimUserVisible = false"> </el-button>
        <el-button type="primary" @click="addTrimUser"
          > </el-button
        >
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
  title=""
  :visible.sync="setRolesDialogVisible"
  @close="setRolesDialogclosed"
  width="50%">
  <div>
    <p>{{userInfo.username}}</p>
    <p>{{userInfo.role_name}}</p>
    <p>
      
      <el-select v-model="selectRoleId" placeholder="">
    <el-option
      v-for="item in rolesList"
      :key="item.id"
      :label="item.roleName"
      :value="item.id">
    </el-option>
  </el-select>
    </p>
  </div>
  <span slot="footer" class="dialog-footer">
    <el-button @click="setRolesDialogVisible = false"> </el-button>
    <el-button type="primary" @click="setRolesDialogVisible = false"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    var checkEmail = (rule, value, callback) => {
      const emailRef = /^[a-zA-Z0-9]+([-_.][a-zA-Z0-9]+)*@[a-zA-Z0-9]+([-_.][a-zA-Z0-9]+)*\.[a-z]{2,}$/
      if (emailRef.test(value)) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    var checkMobile = (rule, value, callback) => {
      const mobileRef = /^1(3[0-9]|4[01456879]|5[0-35-9]|6[2567]|7[0-8]|8[0-9]|9[0-35-9])\d{8}$/
      if (mobileRef.test(value)) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    return {
      // 
      paramsUser: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 2
      },
      usersData: [],
      totalUsers: 0,
      // 
      dialogVisible: false,
      // 
      userForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      userFormRule: {
        username: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 6,
            message: ' 3  6 ',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 12,
            message: ' 6  12 ',
            trigger: 'blur'
          }
        ],
        email: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 20,
            message: ' 6  20 ',
            trigger: 'blur'
          },
          {
            validator: checkEmail,
            trigger: 'blur'
          }
        ],
        mobile: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 11,
            message: ' 6  11 ',
            trigger: 'blur'
          },
          {
            validator: checkMobile,
            trigger: 'blur'
          }
        ]
      },
      // 
      trimUserVisible: false,
      // 
      trimUserForm: {},
      // 
      setRolesDialogVisible: false,
      // 
      userInfo: {},
      // 
      rolesList: [],
      // id
      selectRoleId: ''
    }
  },
  created () {
    this.getUsersData()
  },
  methods: {
    // 
    async getUsersData () {
      const { data: res } = await this.$http.get('users', {
        params: this.paramsUser
      })
      // console.log(res)
      //   this.pagenum = res.data.pagenum
      if (res.meta.status !== 200) {
        return this.$message.error(`${res.meta.msg}`)
      }
      this.totalUsers = res.data.total
      this.usersData = res.data.users
    },
    // 
    handleSizeChange (sizeChange) {
      // console.log(sizeChange)
      this.paramsUser.pagesize = sizeChange
      this.getUsersData()
    },
    // 
    handleCurrentChange (currentSize) {
      // console.log(currentSize)
      this.paramsUser.pagenum = currentSize
      this.getUsersData()
    },
    // 
    async toggleSwitch (switchRole) {
      // console.log(switchRole)
      const { data: res } = await this.$http.put(
        `users/${switchRole.id}/state/${switchRole.mg_state}`
      )
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      } else {
        return this.$message.success(res.meta.msg)
      }
    },
    // 
    searchUser () {
      this.getUsersData()
    },
    // 
    resetUserForm () {
      this.$refs.userFormRef.resetFields()
    },
    // 
    addUser () {
      this.$refs.userFormRef.validate(async (addFlag) => {
        // console.log(addFlag)
        if (addFlag) {
          const { data: res } = await this.$http.post('users', this.userForm)
          if (res.meta.status === 201) {
            this.$message.success(`${res.meta.msg}`)
            // 
            this.dialogVisible = false
            // 
            this.getUsersData()
          } else {
            this.$message(`${res.meta.msg}`)
          }
        } else {
          this.$message.error('')
        }
      })
    },
    // 
    async trimUser (id) {
      // console.log(id)
      this.trimUserVisible = true
      const { data: res } = await this.$http.get(`users/${id}`)
      // console.log(res)
      if (res.meta.status === 200) {
        this.trimUserForm = res.data
        // this.$message.success(res.meta.msg)
      } else {
        this.$message.error(res.meta.msg)
        return false
      }
    },
    // 
    resetTrimUser () {
      this.$refs.trimUserRef.resetFields()
    },
    // 
    addTrimUser () {
      // console.log(this.trimUserForm)
      this.$refs.trimUserRef.validate(async trimFlag => {
        if (trimFlag) {
          const { data: res } = await this.$http.put(`users/${this.trimUserForm.id}`, {
            email: this.trimUserForm.email,
            mobile: this.trimUserForm.mobile
          })
          if (res.meta.status === 200) {
            this.trimUserVisible = false
            this.getUsersData()
            this.$message.success(res.meta.msg)
          } else {
            return this.$message.error(res.meta.msg)
          }
        } else {
          return this.$message.error('')
        }
      })
    },
    // 
    deleteUser (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`users/${id}`)
        console.log(res)
        if (res.meta.status === 200) {
          this.getUsersData()
          this.$message({
            type: 'success',
            message: res.meta.msg
          })
        } else {
          return this.$message.error(res.meta.msg)
        }
      }).catch(() => {
        return this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    async setRoles (userInfo) {
      this.userInfo = userInfo
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status === 200) {
        this.rolesList = res.data
      } else {
        this.$message.error(res.meta.msg)
      }
      this.setRolesDialogVisible = true
    },
    // 
    async setRolesDialogclosed () {
      const { data: res } = await this.$http.put(`users/${this.userInfo.id}/role`, {
        rid: this.selectRoleId
      })
      if (res.meta.status === 200) {
        this.$message.success(res.meta.msg)
        this.getUsersData()
      } else {
        this.$message.error(res.meta.msg)
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-input {
  width: 300px;
  margin-right: 20px;
}
</style>
