<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <div>
          <el-table :data="rightsList" border style="width: 100%" stripe>
              <el-table-column type="index">
            </el-table-column>
            <el-table-column prop="authName" label="" >
            </el-table-column>
            <el-table-column prop="path" label="" >
            </el-table-column>
            <el-table-column prop="level" label="">
                <template v-slot:default="slotProps">
                <el-tag v-if="slotProps.row.level === '0'"></el-tag>
                <el-tag v-else-if="slotProps.row.level === '1'" type="success"></el-tag>
                <el-tag v-else-if="slotProps.row.level === '2'" type="warning"></el-tag>
                </template>
            </el-table-column>
          </el-table>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      rightsList: []
    }
  },
  created () {
    this.getRightsList()
  },
  methods: {
    //  
    async getRightsList () {
      const { data: res } = await this.$http.get('rights/list')
      if (res.meta.status === 200) {
        this.rightsList = res.data
        this.$message.success(res.meta.msg)
      } else {
        return this.$message.error(res.meta.msg)
      }
    }
  }
}
</script>

<style lang="less" scoped>
</style>
