<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="searchGoodsValue"
            @clear="inputCleared"
            clearable
          >
            <el-button slot="append" icon="el-icon-search" @click="searchGoods"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddPage"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="goodsList" border style="width: 100%" stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="goods_name" label="" width="700">
        </el-table-column>
        <el-table-column prop="goods_price" label="()">
        </el-table-column>
        <el-table-column prop="goods_weight" label="" width="90">
        </el-table-column>
        <el-table-column prop="add_time" label="" width="180">
            <template v-slot:default="slotProps">{{slotProps.row.add_time | dateFormat}}</template>
        </el-table-column>
        <el-table-column label="" width="180">
            <template v-slot:default="slotProps">
            <!--  -->
          <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
          <!--  -->
          <el-button type="danger" icon="el-icon-delete" size="mini"
          @click="deleteGoods(slotProps.row)"
            ></el-button
          >
            </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryParams.pagenum"
      :page-sizes="[10, 20, 30, 40]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="goodsTotal">
    </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      searchGoodsValue: '',
      queryParams: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      goodsTotal: 0,
      //   
      goodsList: []
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    async getGoodsList () {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryParams
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      //   console.log(res.data)
      this.goodsTotal = res.data.total
      this.goodsList = res.data.goods
    },
    // 
    handleSizeChange (size) {
    //   console.log(size)
      this.queryParams.pagesize = size
      this.getGoodsList()
    },
    // 
    handleCurrentChange (currentPage) {
    //   console.log(current)
      this.queryParams.pagenum = currentPage
      this.getGoodsList()
    },
    // 
    searchGoods () {
      this.queryParams.query = this.searchGoodsValue
      this.getGoodsList()
    },
    // 
    inputCleared () {
      this.queryParams.query = ''
      this.getGoodsList()
    },
    // 
    deleteGoods (row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`goods/${row.goods_id}`)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.getGoodsList()
        this.$message.success('')
        this.$message({
          type: 'success',
          message: '!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    goAddPage () {
      this.$router.push('/goods/add')
    }
  }
}
</script>

<style lang="less" scoped>
</style>
