<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-alert
        title=""
        type="info"
        :closable="false"
        center
        show-icon
      >
      </el-alert>
      <!--  -->
      <el-steps
        :space="200"
        :active="+activePath"
        finish-status="success"
        align-center
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!--  -->
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          v-model="activePath"
          tab-position="left"
          :before-leave="beforeLeave"
          @tab-click="tabClicked"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addCateForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input
                v-model="addCateForm.goods_price"
                type="number"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input
                v-model="addCateForm.goods_weight"
                type="number"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input
                v-model="addCateForm.goods_number"
                type="number"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                v-model="addCateForm.goods_cat"
                :options="cateList"
                :props="cateProps"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item
              :label="item.attr_name"
              v-for="item in manyTableData"
              :key="item.id"
            >
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  :label="chitem"
                  v-for="(chitem, i) in item.attr_vals"
                  :key="i"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyTableData"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3"
            ><el-upload
              action="http://127.0.0.1:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="handleSuccess"
              list-type="picture"
              :headers="uploadHead"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload></el-tab-pane
          >
          <el-tab-pane label="" name="4">
            <quill-editor v-model="addCateForm.goods_introduce"></quill-editor>
            <el-button type="primary" @click="addFormHandle"
              ></el-button
            >
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="previewDialogVisible" width="50%">
      <img :src="previewImgPath" alt="" style="width: 100%" />
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  data () {
    return {
      activePath: '0',
      addCateForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        goods_cat: [],
        pics: [],
        goods_introduce: '',
        attrs: []
      },
      addCateFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //   
      cateList: [],
      // 
      cateProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children',
        expandTrigger: 'hover'
      },
      //   
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      // fileList: [],
      uploadHead: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      previewDialogVisible: false,
      // 
      previewImgPath: ''
    }
  },
  created () {
    this.getCatelist()
  },
  methods: {
    async getCatelist () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.cateList = res.data
    },
    // 
    handleChange () {
      //   console.log(this.addCateForm.goods_cat)
      if (this.addCateForm.goods_cat.length !== 3) {
        this.addCateForm.goods_cat = []
      }
    },
    // 
    beforeLeave (activeName, oldActiveName) {
      //   console.log(activeName, oldActiveName)
      if (this.addCateForm.goods_cat.length === 3) {
        return true
      }
      this.$message.error('')
      return false
    },
    // 
    async tabClicked () {
      //   console.log(this.activePath)
      if (this.activePath === '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: {
              sel: 'many'
            }
          }
        )
        if (res.meta.status !== 200) {
          this.$message.error(res.meta.msg)
        }
        console.log(res.data)
        res.data.forEach((item) => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })
        this.manyTableData = res.data
      } else if (this.activePath === '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: {
              sel: 'only'
            }
          }
        )
        if (res.meta.status !== 200) {
          this.$message.error(res.meta.msg)
        }
        // console.log(res)
        this.onlyTableData = res.data
      }
    },
    // 
    handlePreview (file) {
      // console.log(file)
      this.previewImgPath = file.response.data.url
      this.previewDialogVisible = true
    },
    // 
    handleRemove (file) {
      const index = this.addCateForm.pics.findIndex((item) => {
        return item.pic === file.response.data.tmp_path
      })
      // console.log(index)
      this.addCateForm.pics.splice(index, 1)
      // console.log(this.addCateForm)
    },
    // 
    handleSuccess (res) {
      // console.log(this.fileList)
      // console.log(res)
      const picInfo = {
        pic: res.data.tmp_path
      }
      this.addCateForm.pics.push(picInfo)
      // console.log(this.addCateForm)
    },
    // 
    addFormHandle () {
      const newForm = _.cloneDeep(this.addCateForm)
      newForm.goods_cat = newForm.goods_cat.join(',')
      // console.log(newForm)
      // 
      this.manyTableData.forEach((item) => {
        const newInfo = {
          attr_id: item.attr_id,
          attr_value: item.attr_vals.join(' ')
        }
        this.addCateForm.attrs.push(newInfo)
      })
      // 
      this.onlyTableData.forEach((item) => {
        const newInfo = {
          attr_id: item.attr_id,
          attr_value: item.attr_vals
        }
        this.addCateForm.attrs.push(newInfo)
      })
      newForm.attrs = this.addCateForm.attrs
      // console.log(newForm)
      this.$refs.addCateFormRef.validate(async (addFlag) => {
        if (!addFlag) {
          return this.$message.error('')
        }
        const { data: res } = await this.$http.post('goods', newForm)
        if (res.meta.status !== 201) {
          return this.$message.error(res.meta.msg)
        }
        this.$message.success('')
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    cateId () {
      if (this.addCateForm.goods_cat.length === 3) {
        return this.addCateForm.goods_cat[2]
      }
      return false
    }
  }
}
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}

.el-button {
  margin-top: 20px;
}
</style>
