<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-alert
        title=""
        type="warning"
        show-icon
        :closable="false"
      >
      </el-alert>
      <!--  -->
      <div>
        <span></span>
        <el-cascader
          v-model="selectedKeys"
          :options="parentList"
          :props="parentListProps"
          @change="handleChange"
          clearable
        ></el-cascader>
      </div>
      <!--  -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            :disabled="isButtonDisabled"
            @click="addDialogVisible = true"
            ></el-button
          >
          <el-table :data="manyList" border>
            <el-table-column type="expand">
              <template v-slot:default="slotProps">
                <el-tag
                  v-for="(item, index) in slotProps.row.attr_vals"
                  :key="index"
                  closable
                  @close="deleteTag(index,slotProps.row)"
                  >{{ item }}</el-tag
                >
                <el-input
                  class="input-new-tag"
                  v-if="slotProps.row.inputVisible"
                  v-model="slotProps.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(slotProps.row)"
                  @blur="handleInputConfirm(slotProps.row)"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(slotProps.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column prop="attr_name" label="">
            </el-table-column>
            <el-table-column label="">
              <template v-slot:default="slotProps">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="editParams(slotProps.row.attr_id)"
                  ></el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="deleteParams(slotProps.row.attr_id)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button
            type="primary"
            :disabled="isButtonDisabled"
            @click="addDialogVisible = true"
            ></el-button
          >
          <el-table :data="onlyList" border>
            <el-table-column type="expand">
              <template v-slot:default="slotProps">
                <el-tag
                  v-for="(item, index) in slotProps.row.attr_vals"
                  :key="index"
                  closable
                  >{{ item }}</el-tag
                >
                <el-input
                  class="input-new-tag"
                  v-if="slotProps.row.inputVisible"
                  v-model="slotProps.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm"
                  @blur="handleInputConfirm"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(slotProps.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column prop="attr_name" label="">
            </el-table-column>
            <el-table-column label="">
              <template v-slot:default="slotProps">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="editParams(slotProps.row.attr_id)"
                  ></el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="deleteParams(slotProps.row.attr_id)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--  -->
    <el-dialog
      :title="'' + titleText"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClose"
    >
      <el-form
        :model="addParamsForm"
        :rules="addParamsFormRules"
        ref="addParamsFormRef"
        label-width="100px"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParamsHandle"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="'' + titleText"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClose"
    >
      <el-form
        :model="editParamsForm"
        :rules="editParamsFormRules"
        ref="editParamsFormRef"
        label-width="100px"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParamsHandle"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      parentList: [],
      selectedKeys: [],
      parentListProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      activeName: 'many',
      // 
      manyList: [],
      // 
      onlyList: [],
      // 
      addDialogVisible: false,
      // 
      addParamsForm: {
        attr_name: ''
      },
      // 
      addParamsFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      editParamsForm: {
        attr_name: ''
      },
      // 
      editParamsFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // id
      attrId: ''
    }
  },
  created () {
    this.getParentList()
  },
  methods: {
    async getParentList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.mag)
      }
      // console.log(res)
      this.parentList = res.data
    },
    // 
    handleChange () {
      if (this.selectedKeys.length !== 3) {
        this.selectedKeys = []
        this.manyList = []
        this.oneList = []
        return false
      }
      this.getParamsList()
    },
    // 
    handleTabClick () {
      this.getParamsList()
    },
    // 
    async getParamsList () {
      const { data: res } = await this.$http.get(
        `categories/${this.catId}/attributes`,
        {
          params: {
            sel: this.activeName
          }
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      } else {
        // console.log(res.data)
        res.data.forEach((item) => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
          item.inputVisible = false
          item.inputValue = ''
        })
        if (this.activeName === 'many') {
          this.manyList = res.data
        } else {
          this.onlyList = res.data
        }
      }
    },
    // 
    addDialogClose () {
      this.$refs.addParamsFormRef.resetFields()
    },
    // 
    addParamsHandle () {
      this.$refs.addParamsFormRef.validate(async (addFlag) => {
        if (!addFlag) return false
        const { data: res } = await this.$http.post(
          `categories/${this.catId}/attributes`,
          {
            attr_name: this.addParamsForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.getParamsList()
        this.addDialogVisible = false
      })
    },
    // 
    async editParams (attrId) {
      // console.log(attrId)
      // this.editParamsForm = this.getParamsById(attrId)
      // console.log(await this.getParamsById(attrId))
      this.attrId = attrId
      this.editParamsForm = await this.getParamsById(attrId)
      this.editDialogVisible = true
    },
    // 
    editDialogClose () {
      this.$refs.editParamsFormRef.resetFields()
    },
    // 
    async editParamsHandle () {
      const { data: res } = await this.$http.put(
        `categories/${this.catId}/attributes/${this.attrId}`,
        {
          attr_name: this.editParamsForm.attr_name,
          attr_sel: this.activeName
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.$message.success('')
      this.attrId = ''
      this.editDialogVisible = false
      this.getParamsList()
    },
    // id
    async getParamsById (id) {
      const { data: res } = await this.$http.get(
        `categories/${this.catId}/attributes/${id}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // console.log(res.data)
      return res.data
    },
    // 
    deleteParams (attrId) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          // console.log(11)
          const { data: res } = await this.$http.delete(
            `categories/${this.catId}/attributes/${attrId}`
          )
          if (res.data.status !== 200) {
            return this.$message.error(res.data.msg)
          }
          this.getParamsList()
          this.$message({
            type: 'success',
            message: '!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // tag 
    handleInputConfirm (row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return false
      }
      row.attr_vals.push(row.inputValue)
      row.inputValue = ''
      this.saveAttributes(row)
      // this.getParamsList()
      row.inputVisible = false
    },
    // tag
    showInput (row) {
      row.inputVisible = true
      // $nextTick
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // tag
    deleteTag (index, row) {
      // const index = row.attr_vals.indexOf(item)
      // console.log(index)
      row.attr_vals.splice(index, 1)
      this.saveAttributes(row)
    },
    // 
    async saveAttributes (row) {
      const { data: res } = await this.$http.put(`categories/${this.catId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      return this.$message.success(res.meta.msg)
    }
  },
  computed: {
    isButtonDisabled () {
      if (this.selectedKeys.length !== 3) {
        return true
      } else {
        return false
      }
    },
    catId () {
      if (this.selectedKeys.length !== 3) {
        return null
      } else {
        return this.selectedKeys[2]
      }
    },
    titleText () {
      if (this.activeName === 'many') {
        return ''
      } else {
        return ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-table {
  margin-top: 20px;
}

.el-tag {
  margin: 5px;
}

.input-new-tag {
  width: 120px;
}
</style>
