<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <el-button type="primary" @click="adddCate"></el-button>
      <!--  -->
      <tree-table
      class="treeTable"
        :data="goodsList"
        :columns="columns"
        :selection-type="false"
        show-index
        index-text="#"
        :expand-type="false"
        :show-row-hover="false"
        border
      >
        <template slot="isOk" slot-scope="goodsScope">
          <i
            class="el-icon-circle-check"
            v-if="goodsScope.row.cat_deleted === false"
            style="color: lightgreen"
          ></i>
          <i class="el-icon-circle-close" v-else style="color: red"></i>
        </template>
        <template slot="sort" slot-scope="goodsScope">
          <el-tag v-if="goodsScope.row.cat_level === 0"></el-tag>
          <el-tag type="success" v-else-if="goodsScope.row.cat_level === 1"
            ></el-tag
          >
          <el-tag type="warning" v-else></el-tag>
        </template>
        <template slot="handle" slot-scope="goodsScope">
          <el-button type="primary" @click="editCate(goodsScope.row)" icon="el-icon-edit" size="mini"
            ></el-button
          >
          <el-button type="danger" @click="deleteCate(goodsScope.row)" icon="el-icon-delete" size="mini"
            ></el-button
          >
        </template>
      </tree-table>
      <!--  -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="paramsGoods.pagenum"
      :page-sizes="[1, 4, 5, 8]"
      :page-size="5"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalGoods">
    </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
  title=""
  :visible.sync="adddCateDialogVisible" @close="resetAddCateFile"
  width="50%">
  <el-form ref="addCateFormRef" :model="addCateForm" :rules="addCateFormRules" label-width="80px">
  <el-form-item label="" prop="cat_name">
    <el-input v-model="addCateForm.cat_name"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-cascader
    v-model="selectedKeys"
    :options="parentList"
    :props="parentListProps"
    @change="handleChange"
    clearable></el-cascader>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="adddCateDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addCateHandle"> </el-button>
  </span>
</el-dialog>
<!--  -->
<el-dialog
  title=""
  :visible.sync="editCateDialogVisible"
  @close="resetEditFields"
  width="50%">
  <el-form ref="editFormRef" :rules="editFormRules" :model="editForm" label-width="80px">
  <el-form-item label="" prop="cat_name">
    <el-input v-model="editForm.cat_name"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editCateDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editFormHandle"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      goodsList: [],
      // 
      paramsGoods: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      //  
      totalGoods: 0,
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          type: 'template',
          template: 'isOk'
        },
        {
          label: '',
          type: 'template',
          template: 'sort'
        },
        {
          label: '',
          type: 'template',
          template: 'handle'
        }
      ],
      //   
      adddCateDialogVisible: false,
      //   
      addCateForm: {
        cat_pid: 0,
        cat_name: '',
        cat_level: 0
      },
      addCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 6, message: ' 1  6 ', trigger: 'blur' }
        ]
      },
      // 
      parentList: [],
      // 
      parentListProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        checkStrictly: true
      },
      // 
      selectedKeys: [],
      // id
      cateId: '',
      // 
      editForm: {
        cat_name: ''
      },
      editFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 6, message: ' 1  6 ', trigger: 'blur' }
        ]
      },
      editCateDialogVisible: false
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    async getGoodsList () {
      const { data: res } = await this.$http.get('categories', {
        params: this.paramsGoods
      })
      //   console.log(res.data)
      this.goodsList = res.data.result
      this.totalGoods = res.data.total
    },
    // 
    handleSizeChange (newSize) {
      this.paramsGoods.pagesize = newSize
      this.getGoodsList()
    },
    // 
    handleCurrentChange (newPage) {
      this.paramsGoods.pagenum = newPage
      this.getGoodsList()
    },
    // 
    adddCate () {
      this.getParentList()
      this.adddCateDialogVisible = true
    },
    // 
    resetAddCateFile () {
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
    },
    // 
    async getParentList () {
      const { data: res } = await this.$http.get('categories', {
        params: {
          type: 2
        }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // console.log(res.data)
      this.parentList = res.data
    },
    // 
    handleChange () {
      // console.log(this.selectedKeys)
      if (this.selectedKeys.length > 0) {
        // id
        this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
        // 
        this.addCateForm.cat_level = this.selectedKeys.length
      } else {
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    // 
    addCateHandle () {
      this.$refs.addCateFormRef.validate(async addCateFlag => {
        // console.log(addCateFlag)
        if (!addCateFlag) {
          return this.$message.error('')
        }
        // console.log(this.addCateForm)
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        if (res.meta.status !== 201) {
          return this.$message.error(res.meta.msg)
        }
        this.getGoodsList()
        this.adddCateDialogVisible = false
      })
    },
    // 
    editCate (cate) {
      this.cateId = cate.cat_id
      this.getCateById()
      this.editCateDialogVisible = true
    },
    // 
    async editFormHandle () {
      const { data: res } = await this.$http.put(`categories/${this.cateId}`, {
        cat_name: this.editForm.cat_name
      })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.getGoodsList()
      // console.log(11)
      this.editCateDialogVisible = false
    },
    // 
    resetEditFields () {
      this.$refs.editFormRef.resetFields()
      this.cateId = ''
    },
    // 
    deleteCate (cate) {
      this.cateId = cate.cat_id
      // console.log(this.cateId)
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`categories/${this.cateId}`)
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        }
        this.getGoodsList()
        this.$message({
          type: 'success',
          message: '!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    async getCateById () {
      const { data: res } = await this.$http.get(`categories/${this.cateId}`)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.editForm.cat_name = res.data.cat_name
    }
  }
}
</script>

<style lang="less" scoped>
.treeTable {
    margin-top: 15px;
}

.el-cascader {
  width: 100%;
}
</style>
